import React from "react";
import ReactDOM from "react-dom";
import logo from "./logo.png";
import logo2 from "./logo2.webp"

class Hello extends React.Component {
  render() {
    const mystyle = {
      textAlign: "center",
      font: "2em Verdana"
    }
    const mystyle2 = {
      color: "#4269e1",
    }
    const mystyle3 = {
      width: "35%"
    }
    const mystyle4 = {
      color: "#dc143c",
    }
    const mystyle5 = {
      width: "100%"
    }
    return (
      <main style={mystyle}>
        <h1 style={mystyle2}>Hello World!</h1> 
        <img style={mystyle3} src={logo} alt="Earth" />
        <h1 style={mystyle4}>Hello React!</h1> 
        <img style={mystyle5} src={logo2} alt="Space" />
      </main>
    );
  }
}

ReactDOM.render(<Hello />, document.querySelector("#root"));
